.tags li, .tags a {
  display: inline-block;
  height: 28px;
  line-height: 28px;
  position: relative;
  font-size: 14px;
  margin-bottom: 16px;
  margin-bottom: 1rem; }

.tags a {
  margin-left: 20px;
  padding: 0 10px 0 12px;
  background: #535953;
  color: #fff;
  text-decoration: none;
  border-bottom-right-radius: 4px;
  border-top-right-radius: 4px; }

.tags a::before {
  content: "";
  position: absolute;
  top: 0;
  left: -14px;
  border-color: transparent #535953 transparent transparent;
  border-style: solid;
  border-width: 14px 14px 14px 0; }

.tags a::after {
  content: "";
  position: absolute;
  top: 12px;
  left: 0;
  width: 6px;
  height: 6px;
  border-radius: 50%;
  background: #fff;
  -webkit-box-shadow: -1px -1px 1px 0 rgba(0, 0, 0, .3);
          box-shadow: -1px -1px 1px 0 rgba(0, 0, 0, .3); }

.tags a:hover {
  background: #373C38; }

.tags a:hover::before {
  border-color: transparent #373C38 transparent transparent; }



